* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  display: flex;
  width: 100vw;
  height: 100vh;
  position: relative;
}

.Main {
  width: 100%;
  height: 100%;
  background-image: url('./images/bg.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: flex;
}

.Sidebar {
  width: 300px;
  min-width: 250px;
  height: 100%;
  border-right: 1px solid black;
  font-size: 1.2rem;
}

.login {
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 1rem 0;
  border-bottom: 1px solid #bebebe;
  font-weight: bolder;
}

.login .image {
  width: 50px;
}

.login .image img {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  border: 1px solid black;
  object-fit: center;
}

.login .title a {
  text-decoration: none;
  display: flex;
  justify-content: flex-end;
  color: red;
}

.AddNewToDo {
  padding: 0.5rem 0;
  border-bottom: 1px solid #bebebe;
}

.AddNewToDo .btn {
  margin: 0 auto;
  width: 70%;
  text-align: center;
}

.AddNewToDo .btn .btn-addNewToDo {
  width: 100%;
  background-color: rgba(17, 17, 126, 0.8);
  color: white;
  padding: 1rem 0;
  border-radius: 8px;
  font-size: 1.2rem;
  cursor: pointer;
  border: none;
}
.container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(29, 29, 32, 0.548);
  z-index: 99;
}

.container .modal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  padding: 0.5rem 1rem;
  background-color: white;
  max-width: 450px;
}

.ProjectForm {
  font-size: 1.5rem;
  width: 100%;
  height: 125px;
}

.ProjectForm input {
  width: 100%;
  border: none;
  font-size: 1.5rem;
  padding: 0.5rem 0;
}

.ProjectForm h3 {
  width: 100%;
}

.ProjectForm .btn {
  position: relative;
  width: 100%;
  margin: 10px 0;
}

.ProjectForm .btn .cancel,
.ProjectForm .btn .confirm {
  position: absolute;
  width: 49%;
  background-color: blue;
  padding: 0.5rem 0;
  color: white;
  font-size: 1.2rem;
  border: none;
  height: fit-content;
  text-align: center;
  cursor: pointer;
}

.ProjectForm .btn .cancel {
  top: 0;
  left: 0;
}

.ProjectForm .btn .confirm {
  top: 0;
  right: 0;
}

/* mối quan hệ giữa .modal và .ProjectForm vs .cancel */

.calendar-content {
  padding: 1rem 0;
  border-bottom: 1px solid #bebebe;
}

.calendar-content .title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.5rem 0.5rem;
}

.projects span {
  padding: 0 0.5rem;
}
.calendar-content .title p {
  padding: 0 0.5rem;
  font-weight: bolder;
}

.calendar-content .calendar {
  padding: 0.5rem 0.5rem;
  cursor: pointer;
}

.projects {
  border-bottom: 1px solid #bebebe;
}
.projects .title {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  padding: 0.5rem 0.5rem;
}

.caretUp {
  cursor: pointer;
  height: 20px;
  width: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #ebebeb;
  transition: background 300ms ease-in-out;
}

.projects .icon {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  width: 60%;
  cursor: pointer;
}

.projects .special {
  padding: 0.5rem 0.5rem;
}
.projects .project {
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0rem;
  height: 35px;
}

.projects .project .numOfToDo {
  height: 20px;
  width: 20px;
  border-radius: 50%;
  background-color: rgba(128, 128, 128, 0.253);
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 15px;
}

.projects .project .ICON {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  height: 100%;
}

.projects .project .rename {
  position: relative;
  padding: 0.5rem 0;
  width: 250px;
  height: 150px;
}

.projects .project .rename .confirm {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
}

.projects .project .rename form input {
  width: 100%;
  border: none;
  padding: 0.5rem 0;
}

.rename .confirm .add,
.cancel {
  width: 49%;
  position: absolute;
  bottom: 5px;
}

.rename .confirm .add {
  left: 0;
}

.rename .confirm .cancel {
  right: 0;
}

.rename .confirm button {
  width: 100%;
  background-color: aquamarine;
  padding: 0.5rem 0;
  border: 1px solid black;
  cursor: pointer;
  position: relative;
}

.Todos {
  background-color: white;
  margin: 1rem 1rem;
  width: 400px;
  height: 500px;
  padding: 0.5rem 0.5rem;
  border-radius: 5px;
  overflow-y: scroll;
}

.Todos .selectedProject {
  padding: 1rem 0;
  font-size: large;
  font-weight: bold;
}
.Todos .todo {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.todo .check {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  width: 15px;
}

.todo .content {
  flex: 1;
  padding: 1rem 1rem;
}

.todo .content .text {
  display: flex;
  align-items: center;
  width: 100%;
}

.todo .content .text .checked {
  position: relative;
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}

.todo .content .text .checked .line {
  height: 2px;
  position: absolute;
  top: 50%;
  width: 0;
  transition: width 250ms ease-in-out;
  background: #bebebe;
}

.todo .content .text .checked .line-through {
  width: 100%;
}

.todo .content .time-day {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.todo .content .time-day .day,
.project {
  padding-left: 15px;
}

.todo .icon span {
  padding: 0 0.5rem;
  cursor: pointer;
}

/* Edit to do */
.EditToDo {
  background-color: white;
  margin: 1rem 1rem;
  width: 400px;
  height: 500px;
  padding: 0 0.5rem;
  border-radius: 5px;
}

.TodoForm {
  font-size: 1.2rem;
}
.TodoForm input {
  width: 100%;
  border: none;
  padding: 1rem 0;
  font-size: 1.2rem;
}

.TodoForm .title span,
.showTime span,
.showDay span {
  padding-left: 10px;
  font-weight: bold;
}

.showTime,
.showDay {
  margin: 1rem 0;
}
.showTime .time,
.showDay .day {
  display: flex;
  justify-content: flex-start;
  padding: 0 1rem;
  width: 80%;
  font-weight: bolder;
}

.TodoForm .projects {
  display: flex;
  flex-wrap: wrap;
}
.TodoForm .project {
  background-color: rgba(13, 121, 121, 0.281);
  margin-left: 5px;
  border-radius: 5px;
  width: fit-content;
  margin-bottom: 10px;
}

.TodoForm .projects .active {
  background: greenyellow;
}

.TodoForm .exit {
  position: absolute;
  top: 5px;
  right: 5px;
  font-size: 30px;
  font-weight: bolder;
  color: yellowgreen;
}

.TodoForm .confirmBtn {
  margin: 10px auto;
  text-align: center;
  background-color: green;
  border-radius: 5px;
  color: white;
  padding: 0.5rem;
  cursor: pointer;
  border: none;
  display: flex;
  justify-content: center;
  width: 90%;
}

.Next7Days .day {
  cursor: pointer;
  padding: 0.5rem 0;
}
